$hoverBreakpoint: 1025px;


.Button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(25% - 1px);
    margin-bottom: 1px;
    border: 0;
    background-color: #ddd;
    cursor: pointer;

    &:hover {
        @media (min-width: $hoverBreakpoint) {   
            background-color: #bbb;
            cursor: pointer;
        }
    }

    
    &:active {
        background-color: #aaa;
    }

    &--is-lighter {
        background-color: #eee;
        }

    &--is-memory {
        width: 30px;
        height: 30px;
        margin-bottom: 2px;
        margin-left: 20px;
        background-color: #ccc;
    }

    &--is-equal {
        background-color: #6d9dc3;

        &:hover {
            @media (min-width: $hoverBreakpoint) {
                background-color: #4599dd;
            }
        }
    }
}
    


    
